
.container {
  width: 20%;
  margin-left: 30px;
  margin-right: 30px; 
  margin-left: 1px;
  margin-right: 1px;
  padding: 0, 0, 0, 0;
}

.card {
  background: #fff;
  border-radius: 2px;
  height: 165px;
  margin: 0.5rem;
  padding: 0.25rem;
  position: relative;
  width: 165px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background:#6CADDC;
}

.card > .img-container > img {
  width: 80%
}

.card > .content {
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 15px;
}

.card > .content > ul {
  list-style-type: none;
}

.card > .content > ul > li {
  margin-top: 18px;
}

