.wrapper {
  padding-top: 50px;
  background: #ECEFF1;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  /* padding: 20px 250px 20px 250px; */
  justify-content: space-around;
  align-content: flex-start;
  overflow: none; 
  /* overflow-x: hidden; */
}

.navbar {
  background: #5133a4;
  max-height: 80px;
  min-height: 60px;
  position: fixed;
  width: 100%;
  box-shadow: 0 14px 28px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.22); 
  color: #fff;
  font-size: calc(10px + 14 * (100vw - 320px)/ 850);
  font-weight: 300;
  z-index: 99;
  white-space: nowrap;
  /* overflow-x: hidden; */
  padding-top: 0px;
}

.navbar {
  display: block;
}

.navbar ul {
  margin: 0;
  padding: 0;
}

ul {
  display: block;
  list-style-type: disc;
  -webkit-margin-before: 1em;
          margin-block-start: 1em;
  -webkit-margin-after: 1em;
          margin-block-end: 1em;
  -webkit-margin-start: 0px;
          margin-inline-start: 0px;
  -webkit-margin-end: 0px;
          margin-inline-end: 0px;
  -webkit-padding-start: 40px;
          padding-inline-start: 40px;
}

.navbar li {
  display: inline-block;
  padding: 26px 0px 26px 0px;
  width: 33.3%;
  text-align: center;
}

a {
  color: #EB6864;
  text-decoration: none;
  background-color: transparent;
  font-weight: 700;
  text-align: left;
}

a:hover {
  color: none;
}
.headerMain {
  background-color: #1e293d;
  /* background-color: #2c323c; */
  background-image: url(/../../../public/assets/images/planets.jpg);    /* (/4de356f…/00972/assets/images/geometry.svg); */
  background-repeat: repeat;
  color: #fff;
  width: 100%;
  max-height: 450px;
  min-height: 300px;
  padding: 120px 15px;
  padding-bottom: 310px;
  text-align: center;
  margin-bottom: 30px;  
}

header {
  display: block;
}

.headerMain, h1 {
  margin: 0px, 0px, 30px, 0px;
  font-size: 4rem;
}

h1 {
  display: block;
  /* font-size: 2em; */
  -webkit-margin-before: 0.67em;
          margin-block-start: 0.67em;
  -webkit-margin-after: 0.67em;
          margin-block-end: 0.67em;
  -webkit-margin-start: 0px;
          margin-inline-start: 0px;
  -webkit-margin-end: 0px;
          margin-inline-end: 0px;
  font-weight: bold;
}

h2 {
  display: block;
  font-size: 0.35em;
  -webkit-margin-before: 0.83em;
          margin-block-start: 0.83em;
  -webkit-margin-after: 0.83em;
          margin-block-end: 0.83em;
  -webkit-margin-start: 0px;
          margin-inline-start: 0px;
  -webkit-margin-end: 0px;
          margin-inline-end: 0px;
  font-weight: bold;
  line-height: 1.15;
}



.footer {
  background: #4e4864;
  height: 65px;
  position: relative;
  color: #fff;
  overflow: auto;
  display: block;
  width: 100%;
  position: relative;
  text-align: center;
  margin-top: 30px;
}

footer {
  display: block;
}

.footer>.bottom {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background: #5133a4;
  padding: 8px;
  font-weight: 600;
  margin-bottom: 0px;
}
* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root, #root>div {
  padding: 0;
  margin: 0;
  height: 100%;
}

html {
  line-height: 1.15;
} 

head {
  display: none;
}

body {
  display: block;
}

div {
  display: block;
}




