.headerMain {
  background-color: #1e293d;
  /* background-color: #2c323c; */
  background-image: url(/../../../public/assets/images/planets.jpg);    /* (/4de356f…/00972/assets/images/geometry.svg); */
  background-repeat: repeat;
  color: #fff;
  width: 100%;
  max-height: 450px;
  min-height: 300px;
  padding: 120px 15px;
  padding-bottom: 310px;
  text-align: center;
  margin-bottom: 30px;  
}

header {
  display: block;
}

.headerMain, h1 {
  margin: 0px, 0px, 30px, 0px;
  font-size: 4rem;
}

h1 {
  display: block;
  /* font-size: 2em; */
  margin-block-start: 0.67em;
  margin-block-end: 0.67em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  font-weight: bold;
}

h2 {
  display: block;
  font-size: 0.35em;
  margin-block-start: 0.83em;
  margin-block-end: 0.83em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  font-weight: bold;
  line-height: 1.15;
}


